import router from './router'
import store from './store'
import { Message } from 'element-ui'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
import getPageTitle from '@/utils/get-page-title'
import {constantRoutes} from "@/router/"

NProgress.configure({ showSpinner: true }) // NProgress Configuration

const whiteList = ['/login'] // no redirect whitelist
const navList = constantRoutes[0].children.map((item) => {
    return '/' + item.path
})
router.beforeEach(async(to, from, next) => {
    // start progress bar
    NProgress.start()
    // set page title

    if (navList.indexOf(to.path) > -1) {
        store.commit("nav/SET_NAV_LINK", to.path);
    } else if (to.path === '/blog/listbycat') {
        store.commit("nav/SET_NAV_LINK", '/category');
    } else if (to.path === '/blog/listbytag') {
        store.commit("nav/SET_NAV_LINK", '/tag');
    }
    document.title = getPageTitle(to.meta.title)
    next()
})

router.afterEach(() => {
    // finish progress bar
    NProgress.done()
})