<template>
  <div class="header-inner" :class="{'site-nav-on':this.$store.state.nav.showNav}">

    <nav ref="nav" class="site-nav" :style="styleObj">
      <ul class="main-menu menu">
        <nav-item navName="首页" navLink="/index" navIcon="home" />
        <nav-item
          navName="归档"
          navLink="/blog/list"
          :navCount="this.$store.state.nav.blogTotal"
          navIcon="archive"
        />
        <nav-item
          navName="分类"
          navLink="/category"
          :navCount="this.$store.state.nav.categoryTotal"
          navIcon="book"
        />
        <nav-item 
        navName="标签" 
        navLink="/tag" 
        :navCount="this.$store.state.nav.tagTotal" 
        navIcon="tags" />
        <nav-item navName="关于" navLink="/about" navIcon="user" />
      </ul>
    </nav>
  </div>
</template>

<script>
import NavItem from "./NavItem";

// import {constantRoutes} from "@/router/"
export default {
  name: "SiteNav",
  data() {
    return {
      styleObj:{
        '--scroll-height': '0px'
      }
    };
  },
  components: {
    NavItem,
  },
  mounted() {
    this.styleObj['--scroll-height'] = this.$refs.nav.scrollHeight + 'px'
  },
};
</script>
<style>
@media (max-width: 991px) {
  .header-inner .site-nav-on .site-nav {
    height: var(--scroll-height);
  }
}
</style>