const state = {
  navLink: '/index',
  showNav:false,
  blogTotal:3,
  categoryTotal:4,
  tagTotal:5
}

const mutations = {
  SET_NAV_LINK: (state, navLink) => {
    state.navLink = navLink
  },
  SET_SHOW_NAV: (state, showNav) => {
    state.showNav = showNav
  },
  SET_BLOG_TOTAL: (state, blogTotal) => {
    state.blogTotal = blogTotal
  },
  SET_CATEGORY_TOTAL: (state, categoryTotal) => {
    state.categoryTotal = categoryTotal
  },
  SET_TAG_TOTAL: (state, tagTotal) => {
    state.tagTotal = tagTotal
  },
}

const actions = {
  setNavLink({ commit, value }) {
    commit('SET_NAV_LINK', value)
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}

